html,
body {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: #fff;
}

#root,
.App,
.content {
  width: 100%;
  height: 100%;
}

// 全局变量
$body-font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
  'PingFang SC', 'Microsoft YaHei', 'Hiragino Sans GB', 'Segoe UI',
  'Helvetica Neue', Helvetica, Arial, Monaco, Menlo, Consolas, sans-serif;

$theme-color: rgba(0, 0, 0, 0.85);
$theme-opacity-color: rgba(255, 255, 255, 0.1);
$border-radius: 10px;
$border: 1px solid rgba(255, 255, 255, 0.1);
$box-shadow: 0 2px 3px 1px rgba(0, 0, 0, 0.1);
$font-size: 14px;

.box-basic {
  background: rgba(255, 255, 255, 0.1);
  border-radius: $border-radius;
  border: 1px solid #eeeeee;
  box-shadow: $box-shadow;
}

.button-basic {
  font-size: 16px;
  color: rgba(0, 0, 0, 0.85);
}

// TAB 全局样式
.view-box-wrap {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 100%;

  & > ul {
    width: 60%;
    padding-top: 20px;

    & > li {
      width: 100%;
      border-bottom: 1px solid #f0f0f0;
      padding: 20px 10px 20px 20px;
      display: flex;
      align-items: center;
      flex-flow: row wrap;
      justify-content: space-between;

      &:last-child {
        border-bottom: none;
      }

      .icon-box,
      .controls-name,
      .controls-state,
      .controls-switch {
        display: flex;
        justify-content: center;
      }

      .icon-box {
        width: 10%;
        max-width: 10%;
      }

      .controls-name {
        width: 20%;
        max-width: 20%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
      }

      .controls-state {
        width: 30%;
        max-width: 30%;
      }

      .controls-on {
        width: 30%;
        max-width: 30%;
        text-align: center;
      }

      .education-list {
        width: 650%;
        max-width: 65%;
      }

      .controls-switch {
        width: 30%;
        max-width: 30%;
      }
    }
  }

  .view-box {
    width: 40%;
    background: #f8f8f8;
    padding: 40px;
    display: flex;
    flex-direction: column;

    .view-box-first {
      width: 100%;
      height: 400px;
      border: 1px solid #eee;
      box-shadow: 0 2px 3px 1px rgba(0, 0, 0, 0.1);
      margin-bottom: 40px;
    }

    .view-box-last {
      width: 100%;
      height: 400px;
      border: 1px solid #eee;
      box-shadow: 0 2px 3px 1px rgba(0, 0, 0, 0.1);
      padding: 40px 20px;
    }
  }
}

// antd 全局 TAB
.ant-tabs-nav {
  margin-bottom: 0 !important;
}

// 小屏幕适应
@media (max-width: 1440px) {
  // 图标
  .anticon {
    transform: scale(0.8);
  }

  // 导航
  .home-menu .ant-menu {
    width: 160px !important;
  }

  // 登录页面
  .login-box-wrap .login-box {
    margin: 150px auto;
  }

  // 首页
  .home-box {
    .home-banner {
      width: 800px !important;
    }

    nav {
      width: 800px;
      margin: 25px auto 0;

      a {
        width: 140px;
        height: 140px;
      }
    }
  }

  // 图表
  .chart-box {
    padding: 20px !important;

    .chart-component {
      margin-bottom: 20px !important;
      height: 260px !important;
    }
  }

  .chart-list-box .chart-box {
    height: 250px !important;
  }

  // 智慧家庭
  .family-home {
    padding: 20px 20px 0 !important;

    .music-box {
      left: -145px !important;
      width: 130px !important;
    }

    .controls-list {
      padding: 10px !important;
    }

    .access-list {
      .btn-group .ant-btn {
        padding: 0 3px !important;
      }
    }

    .view-box {
      padding: 20px !important;
      .view-box-first {
        height: 120px !important;
        line-height: 120px !important;
        margin-bottom: 20px !important;
      }
    }
  }

  // 智慧酒店
  .park-hub {
    .park-panel-box li {
      height: 30px;
      line-height: 30px;
    }
    .view-box {
      padding: 20px !important;
      .view-box-first {
        height: 255px !important;
        margin-bottom: 20px !important;
      }
      .park-panel-wrap {
        padding: 10px 20px !important;
      }

      .view-box-last {
        padding: 10px 5px;
        height: 280px;
      }
    }
  }

  // 智慧教育
  .education-hub {
    .view-box {
      padding: 20px !important;
      .view-box-first {
        height: 265px !important;
        margin-bottom: 20px !important;
        .education-control-wrap {
          padding: 10px 20px;
        }
      }

      .view-box-last {
        padding: 10px 5px !important;
        height: 280px !important;
      }
    }
  }
}


